/**
 * Klasa umoliwia zarzdzanie cyklem ycia obiektw powizanych z danym kontekstem.
 * Jeli dany kontekst (np strona) zostaje zniszczony, to automatycznie zostaje rwnie wywoana funkcja zwrotna
 * [onContextDestroy] metody [add]
 */

import * as Check from "../utils/Check";
import MapList from "../utils/MapList";

const map: Map<any, object[]> = new Map();

/**
 * Dodaje klas do listy. Metod naley uywa rozwanie. Referencje przetrzymywane s statycznie i yj tak dugo, a
 * zostanie wywoana metoda [contextDestroyed] dla tego samego obiektu kontekstu co w metodzie add. Jeli metoda
 * [contextDestroyed] nie zostanie wywoana \, powstanie wyciek pamici
 * @param context
 * @param object
 * @param onContextDestroy
 */

export function add(context: any, object: any, onContextDestroy: (object: any, context: any) => void) {
    if (context === null || context === undefined)
        return;

    const arr: [] = map.get(context);
    if (!arr) return;

    arr.push({
        object: object,
        onContextDestroy: Check.isFunction(onContextDestroy)
    });
}

export function contextCreated(context: any) {
    if (map.has(context))
        throw new Error("Kontekst ju istnieje");
    map.set(context, []);
}

export function contextDestroyed(context: any) {
    const arr: [] = map.get(context);
    if (!arr)
        throw new Error("Kontekst nie istnieje");

    map.delete(context);

    arr.forEach(o => {
        o.onContextDestroy(o.object, context);
    });
}

export function getMap(): MapList<any, object> {
    return map;
}



